}
.tb {
  display: table;
  width: 100%;
}

.td {
  display: table-cell;
  vertical-align: middle;
}

.input-bar,
.button-bar {
  color: white;
  font-family: Nunito;
  padding: 0;
  margin: 0;
  border: 0;
  background-color: transparent;
  outline: none;
}

.container-search {
  position: absolute;
  top: 5%;
  left: 0;
  right: 0;
  width: 100%;
  padding: 20px;
  border-radius: 20px;
  -webkit-transform: scale(0.6);
          transform: scale(0.6);
}

.input-bar[type="text"] {
  width: 100%;
  height: 100%;
  font-size: 60px;
  line-height: 1;
}

.cover {
  width: 1px;
  padding-left: 35px;
}

.button-bar {
  position: relative;
  display: block;
  width: 84px;
  height: 96px;
  cursor: pointer;
  outline: none !important;
}

.button-bar:hover {
  outline: none;
}

.circle {
  position: relative;
  top: -8px;
  left: 0;
  width: 65px;
  height: 65px;
  margin-top: 0;
  border-width: 15px;
  border: 15px solid white;
  background-color: transparent;
  border-radius: 50%;
  transition: 0.5s ease all;
}

.button-span {
  position: absolute;
  top: 68px;
  left: 43px;
  display: block;
  width: 45px;
  height: 15px;
  background-color: transparent;
  border-radius: 10px;
  -webkit-transform: rotateZ(52deg);
          transform: rotateZ(52deg);
  transition: 0.5s ease all;
}

.button-span:before,
.button-span:after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 45px;
  height: 15px;
  background-color: white;
  border-radius: 10px;
  -webkit-transform: rotateZ(0);
          transform: rotateZ(0);
  transition: 0.5s ease all;
}

#cover:hover .circle {
  top: -1px;
  width: 67px;
  height: 15px;
  border-width: 0;
  background-color: white;
  border-radius: 20px;
}

#cover:hover .button-span {
  top: 50%;
  left: 56px;
  width: 25px;
  margin-top: -9px;
  -webkit-transform: rotateZ(0);
          transform: rotateZ(0);
}

#cover:hover .button-span:before {
  bottom: 11px;
  -webkit-transform: rotateZ(52deg);
          transform: rotateZ(52deg);
}

#cover:hover .button-span:after {
  bottom: -11px;
  -webkit-transform: rotateZ(-52deg);
          transform: rotateZ(-52deg);
}
#cover:hover .button-span:before,
#cover:hover .button-span:after {
  right: -6px;
  width: 40px;
  background-color: white;
}

